header {
  width: 100%;
  background: var(--gradient-color);
}

.header-container {
  height: 50px;
  align-items: center;
  display: flex;
}

.nav {
  display: none;
}

.soc-link, .social-item {
  width: 29px;
  height: 29px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.soc-and-theme {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.social-list {
  height: 29px;
  flex-direction: row;
  gap: 5px;
  display: flex;
}

.toggle-theme {
  width: 54px;
  height: 29px;
  background-color: var(--white-color);
  cursor: pointer;
  transition: var(--transition-change);
  border-radius: 20px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.toggle-theme.active {
  background-color: var(--dark-blue);
}

.toggle-theme:before {
  content: "";
  width: 25px;
  height: 25px;
  background-color: var(--dark-blue);
  transition: var(--transition-change);
  background-image: url("sun.863539a9.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 17px;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
}

.toggle-theme.active:before {
  background-color: var(--white-color);
  background-image: url("moon.8f3d5eb2.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 17px;
  left: calc(100% - 27px);
}

@media screen and (min-width: 768px) {
  .header-container {
    justify-content: space-between;
  }

  .nav {
    display: block;
  }

  .nav-list .nav-item:nth-child(4) {
    display: none;
  }

  .nav-list {
    height: 18px;
    width: 371.3px;
    flex-direction: row;
    justify-content: space-between;
    display: flex;
  }

  .nav-item {
    justify-content: center;
    align-items: center;
  }

  .had-nav-link {
    letter-spacing: .04em;
    color: var(--white-color);
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 129%;
    display: flex;
  }

  .soc-and-theme {
    width: 268px;
    justify-content: space-between;
  }
}

@media screen and (min-width: 1440px) {
  .header-container {
    max-width: 1170px;
  }

  .nav-list .nav-item:nth-child(4) {
    display: block;
  }

  .nav-list {
    width: 504.3px;
  }

  .soc-link:hover svg {
    transition: var(--transition-change);
    scale: 1.6;
  }

  .nav-item:hover .had-nav-link, .nav-item:focus .had-nav-link {
    color: var(--dark-blue);
    transition: var(--transition-change);
  }

  .nav-item:hover .nav-icon, .nav-item:focus .nav-icon {
    fill: var(--dark-blue);
    transition: var(--transition-change);
  }

  .soc-and-theme {
    width: 272px;
  }
}

body, p, h1, h2, h3, ol, ul, fieldset, input, button, textarea {
  margin: 0;
  padding: 0;
}

ol, ul {
  list-style: none;
}

button {
  cursor: pointer;
  background: none;
  border: 0;
  outline: 0;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
